@font-face {
  font-family: 'TitanOne';
  src: url(fonts/TitanOne-Regular.ttf);
}

@font-face {
  font-family: 'DancingScript';
  src: url(fonts/DancingScript-Regular.ttf);
}
.App {
  text-align: center;
  position: relative;
}
.ApproveButton{
  background-color: var(--red);
  color: white;
  
}
.DenyButton{
  background-color: black;
  color: white;
}
.DenyButton.DenyButton:hover{
  background-color: white;
}
h1, h2, h3{
  font-family: 'TitanOne';
}
p{
  font-family: 'DancingScript';
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

:root{
  --red: #C03221;
  --pink: #FFE3ED;
  --orange: #FF9B42;
}
.Information{
  background-color: rgba(255, 255, 255, 0.8);
  width: 70%;
  padding: 50px;
  border-radius: 20px;
  box-shadow: 0 0 40px white;
}

#Content {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  filter:blur(20px);
  transition: .25s ease-in-out;
  background-color: rgba(0,0,0,0.5);
}
.App-link {
  color: #61dafb;
}
.HeadingTitle{
  color: var(--red);

  font-size: 60px;
}
#Overlay{
  position: absolute;
  top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  background-color: white;
  color: var(--red);
  border-radius: 20px;
  padding: 60px;
  box-shadow: 0 0 40px black;
  animation: tilt-shaking infinite;
  
}
.BEEGApproveButton{
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background-color: var(--red);
  font-size: 21px;
  font-weight: bold;
  color: white;
  display: none;
}
#Overlay > button{
  font-size: 21px;
  font-weight: bold;
  padding: 10px 20px;
  margin: 20px;
  border-radius: 15px;
}

.UniverseImg{
  width: 600px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes tilt-shaking {
  0% { transform: translate(-50%, -50%) rotate(0deg); }
  25% { transform:translate(-50%, -50%) rotate(5deg); }
  50% { transform:translate(-50%, -50%) rotate(0eg); }
  75% { transform:translate(-50%, -50%) rotate(-5deg); }
  100% { transform:translate(-50%, -50%) rotate(0deg); }
}